function createCarousel() {
  const root = document.getElementById("root");

  const carouselContainer = document.createElement("section");
  carouselContainer.classList.add("carousel-container");
  root.append(carouselContainer);

  const leftCarouselBtn = document.createElement("button");
  leftCarouselBtn.classList.add("carousel-btn", "left");
  const leftArrowImg = document.createElement("img");
  leftCarouselBtn.appendChild(leftArrowImg);
  carouselContainer.append(leftCarouselBtn);

  const carousel = document.createElement("div");
  carousel.classList.add("carousel");
  carouselContainer.append(carousel);

  const carouselLine = document.createElement("div");
  carouselLine.classList.add("carousel-line");
  carousel.append(carouselLine);

  const rightCarouselBtn = document.createElement("button");
  rightCarouselBtn.classList.add("carousel-btn", "right");
  const rightArrowImg = document.createElement("img");
  rightCarouselBtn.appendChild(rightArrowImg);
  carouselContainer.append(rightCarouselBtn);

  fetch("https://67376867aafa2ef22233bb01.mockapi.io/el/carousel")
  .then((response) => {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —É—Å–ø–µ—à–µ–Ω –ª–∏ –æ—Ç–≤–µ—Ç (–∫–æ–¥ —Å–æ—Å—Ç–æ—è–Ω–∏—è –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ 200-299)
    if (!response.ok) {
      throw new Error('Network response was not ok: ' + response.statusText);
    }
    return response.json();
  })
  .then((json) => {
    localStorage.setItem("image", JSON.stringify(json));
    renderCarouselItem(); // –í—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ –ø–æ—Å–ª–µ –ø–æ–ª—É—á–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö
  })
  .catch((error) => {
    console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö:", error);
  });

  const carouselArr = [];

  function renderCarouselItem() {
    const cardsArr = JSON.parse(localStorage.getItem("image"));

    if (cardsArr) {
      // –û—á–∏—Å—Ç–∏–º carouselArr –ø–µ—Ä–µ–¥ –Ω–æ–≤—ã–º –∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ–º
      carouselArr.length = 0;

      cardsArr.forEach((element) => {
        const carouselItem = element.avatar;
        carouselArr.push(carouselItem);
      });

      carouselArr.forEach((element) => {
        const carouselImg = document.createElement("img");
        carouselImg.classList.add("carousel-img");
        carouselImg.src = element;
        carouselLine.append(carouselImg);
      });
      init(); // –í—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ –ø–æ—Å–ª–µ —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
    }
  }

  let count = 0;
  let width;

  function init() {
    width = document.querySelector(".carousel").offsetWidth;
    carouselLine.style.width = width * carouselArr.length + "px"; // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω—É–∂–Ω—É—é —à–∏—Ä–∏–Ω—É
    const carouselImgs = document.querySelectorAll(".carousel-img");
    carouselImgs.forEach((img) => {
      img.style.width = width + "px";
      img.style.height = "auto";
    });
    rollCarousel(); // –§—É–Ω–∫—Ü–∏—è —Å–º–µ—â–µ–Ω–∏—è –ø—Ä–∏ –ø–µ—Ä–µ—Å—á–µ—Ç–µ (—Ñ–∏—à–∫–∞!!!)
    updateIndicators(); // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏
  }
  window.addEventListener("resize", init);

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –Ω–∞–∂–∞—Ç–∏—è –∫–Ω–æ–ø–∫–∏ "–≤–ø—Ä–∞–≤–æ"
  rightCarouselBtn.addEventListener("click", function () {
    count++;
    if (count >= carouselArr.length) {
      count = 0; // –í–µ—Ä–Ω—É—Ç—å—Å—è –∫ –ø–µ—Ä–≤–æ–π –∫–∞—Ä—Ç–∏–Ω–∫–µ, –µ—Å–ª–∏ –¥–æ—Å—Ç–∏–≥–ª–∏ –∫–æ–Ω—Ü–∞
    }
    rollCarousel();
    updateIndicators(); // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã
  });

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –Ω–∞–∂–∞—Ç–∏—è –∫–Ω–æ–ø–∫–∏ "–≤–ª–µ–≤–æ"
  leftCarouselBtn.addEventListener("click", function () {
    count--;
    if (count < 0) {
      count = carouselArr.length - 1; // –í–µ—Ä–Ω—É—Ç—å—Å—è –∫ –ø–æ—Å–ª–µ–¥–Ω–µ–π –∫–∞—Ä—Ç–∏–Ω–∫–µ, –µ—Å–ª–∏ –¥–æ—Å—Ç–∏–≥–ª–∏ –Ω–∞—á–∞–ª–∞
    }
    rollCarousel();
    updateIndicators(); // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã
  });

  function rollCarousel() {
    carouselLine.style.transform = "translate(-" + count * width + "px)";
  }

  // –°–æ–∑–¥–∞–µ–º —Å–ø–∏—Å–æ–∫ –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–æ–≤
  const indicators = document.createElement("ol");
  indicators.classList.add("carousel-indicators");

  // –û–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–ª–∞–π–¥–æ–≤
  const totalSlides = 3;

  for (let i = 0; i < totalSlides; i++) {
    const indicator = document.createElement("li");
    indicator.classList.add(`carousel-indicator-${i + 1}`); // –î–æ–±–∞–≤–ª—è–µ–º —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π –∫–ª–∞—Å—Å
    indicator.dataset.index = i; // –°–æ—Ö—Ä–∞–Ω—è–µ–º –∏–Ω–¥–µ–∫—Å –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–∞ –¥–ª—è –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    if (i === 0) {
      // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –ø–µ—Ä–≤–æ–≥–æ –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–∞
      indicator.classList.add("active");
    }
    indicators.append(indicator); // –î–æ–±–∞–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä –≤ —Å–ø–∏—Å–æ–∫
  }
  carouselContainer.append(indicators); // –î–æ–±–∞–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã –≤ —Å–ª–∞–π–¥–µ—Ä

  function updateIndicators() {
    const indicatorElements = indicators.querySelectorAll("li");

    // –£–¥–∞–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π –∫–ª–∞—Å—Å —É –≤—Å–µ—Ö –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–æ–≤
    indicatorElements.forEach((indicator) => {
      indicator.classList.remove("active");
    });

    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è —Ç–µ–∫—É—â–µ–≥–æ –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–∞
    indicatorElements[count].classList.add("active");
  }

  // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞ –Ω–∞ –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–∞—Ö
  indicators.addEventListener("click", (event) => {
    if (event.target.tagName === "LI") {
      count = parseInt(event.target.dataset.index);
      rollCarousel();
      updateIndicators();
    }
  });

 //Swipe 
  carousel.addEventListener("touchstart", handleTouchStart, false);
  carouselLine.addEventListener("touchmove", handleTouchMove, false);

  let axisX = 0;//—Ö—Ä–∞–Ω–µ–Ω–∏—è –Ω–∞—á–∞–ª—å–Ω–æ–≥–æ –ø–æ–ª–æ–∂–µ–Ω–∏—è –∫–∞—Å–∞–Ω–∏—è.

   function handleTouchStart(event) {
   const touches = event.touches[0];
   axisX = touches.clientX;
  }

  function handleTouchMove(event) {
   if (axisX == 0) {
   return false;
  }
  let moveX = event.touches[0].clientX;
  let xDiff = axisX - moveX;
  if (xDiff > 0) { 
  rightMove();// –§—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –æ—Ç–≤–µ—á–∞–µ—Ç –∑–∞ –¥–≤–∏–∂–µ–Ω–∏–µ –≤–ø—Ä–∞–≤–æ
  } else {
  leftMove();// –§—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –æ—Ç–≤–µ—á–∞–µ—Ç –∑–∞ –¥–≤–∏–∂–µ–Ω–∏–µ –≤–ª–µ–≤–æ
 }
   axisX = 0;// –°–±—Ä–æ—Å
  }
}
export default createCarousel;
